.work-ex-card{
    background: #130f2a;
    border-radius: 0.65rem;
    border: 1.5 solid  rgb(221, 13, 13);
    padding: 1.5rem;
    margin: 0 1rem;
}

.work-ex-card h6{
    font-size: 1.05rem;
    font-weight: 500;
    margin-bottom: 0.7rem;
}

.work-duration{
    display: inline-block;
    font-size: 0.9rem;
    font-weight: 400;
    background: #b74040;
    border-radius: 0.3rem;
    padding: 0.4rem 0.6rem;
    margin-bottom: 1.3rem;
}

.work-ex-card ul li{
    list-style: none;
    font-size: 0.8rem;
    font-weight: 400;
    margin-bottom: 0.5rem;
    position: relative;
}

.work-ex-card ul li::after{
    content:  " ";
    width: 0.5rem;
    height: 0.5rem;
    background-color: rgb(221, 13, 13);
    border-radius: 0.5rem;
    position: absolute;
    left: -1.3rem;
    top: 0.35rem;
}